@import "assets/fonts/style.css";
@import "assets/colors.css";

html, body {
    font-family: 'SF Display', 'Roboto', sans-serif;
    padding: 0;
    margin: 0;
    height: 100vh;
    width: 100%;
}

.navigation-toggle-user, .navigation-menu{
    display: none;
}

.App {
    text-align: center;
}

.clear {
    clear: both;
}

.btn-red {
    color: #cb3636;
    border-color: #cb3636 !important;
}

.error-btn-container {
    height: 127px;
    float: right;
    line-height: 127px;
    padding-right: 23px;
    vertical-align: middle;
}

.wrapper {
    width: 1088px;
    margin: 0 auto;
}

.text-blue {
    color: var(--azul);
}

.btn {
    border-radius: 20px;
    box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.16);
    padding: 8px 15px;
    border: 0;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.19;
    text-decoration: none;
    background-color: var(--white);

    &.blue {
        background-color: #3653cb;
        color: var(--white);
    }

    &.gray {
        background-color: var(--brownish-grey);
        color: var(--white);
    }

    &.bordered {
        border: solid 2px var(--bluey-grey);
    }

    &.icon-button {
        color: var(--bluey-grey);
        padding: 8px 8px;
        line-height: 0;
    }
}

input {
    width: 100%;
    height: 36px;
    border-radius: 5px;
    background-color: #f5f6f7;
    display: block;
    border: 0;
    padding: 0 3%;
}

label {
    display: block;
    font-size: 15px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.2;
    letter-spacing: normal;
    text-align: left;
    color: #2a2f33;
    padding: 20px 0 5px 0;
}

textarea {
    border-radius: 5px;
    background-color: #f5f6f7;
    display: block;
    width: 100%;
    border: 0;
}

.error {
    background: linear-gradient(118deg, #FFFFFF 23%, #F1F4F9 100%);
    overflow: hidden;
    padding: 0;
    margin: 0;
    height: 100vh;
    width: 100%;

    &:before {
        content: ' ';
        position: fixed;
        right: 0;
        top: 0;
        width: 50%;
        height: 100vh;
        background: url("./assets/svg/octagonal-gray.svg") left center no-repeat;
        -webkit-background-size: auto 100%;
        background-size: 205% auto;
    }

    .socials {
        position: fixed;
        right: 10px;
        bottom: 8vh;
        z-index: 1;

        .socialBar {
            border: 1px solid #1d2229;
            width: 0;
            height: 20vh;
            margin: 20px auto;
        }

        .social {
            width: 30px;
            height: 30px;
            display: block;
            color: #1d2229;
            border-radius: 30px;
            border: solid 1px #1d2229;
            text-decoration: none;
            padding: 5px 5px 4px 4px;
            margin: 10px;
            font-size: 18px;
        }
    }

    .contents {
        position: fixed;
        top: 25vh;
        left: 55%;
        z-index: 1;
        text-align: left;
        font-size: 28px;
        right: 10%;

        h1 {
            font-size: 102px;
            font-weight: bold;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.2;
            letter-spacing: normal;
            text-align: left;
            color: #1d2229;
        }

        h2 {
            font-size: 35px;
            font-weight: 600;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.17;
            letter-spacing: normal;
            text-align: left;
            color: #1d2229;
        }

        a {
            margin-top: 20px;
            border-radius: 20px;
            box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.16);
            background-color: var(--brownish-grey);
            display: inline-block;
            text-decoration: none;
            padding: 15px 40px;
            color: var(--white);
        }

    }
}

.search {
    width: 100%;
    display: table;
    height: 70px;
    border-radius: 70px;
    box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.16);
    background-color: var(--white);
    z-index: 1;

    i {
        width: 40px;
        height: 40px;
        font-size: 40px;
        padding: 15px;
        cursor: pointer;
        display: table-cell;
    }

    input {
        border: 0;
        font-size: 26px;
        height: 70px;
        background: transparent;
        outline: none;
        padding: 0 10px;
        display: table-cell;
    }

}

.header {
    width: 100%;
    min-height: 333px;
    background-color: var(--pale-grey);
    position: relative;

    .logo {
        height: 110px;
        width: 110px;
        /*margin: -40px 0;*/
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        color: #2a2f33;
        background: url("./assets/logo.svg") left no-repeat no-repeat;
        background-size: 100px 100px;
        display: inline-block;
        float: left;
    }

    .title {
        position: absolute;
        bottom: 55px;

        font-size: 64px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.19;
        letter-spacing: normal;
        text-align: left;
        color: #2a2f33;
    }

    .search {
        position: absolute;
        bottom: -35px;
    }

    .main-menu-bar {
        .menu-bar {
            list-style-type: none;
            margin: 28px 0 0 80px;
            padding: 0;
            overflow: hidden;
            display: inline-block;
            float: left;

            li {
                float: left;

                a {
                    display: block;
                    padding: 14px 16px;
                    text-decoration: none;
                    font-size: 19px;
                    font-weight: bold;
                    font-stretch: normal;
                    font-style: normal;
                    line-height: 1.21;
                    letter-spacing: normal;
                    text-align: center;
                    color: #2a2f33;
                }
            }
        }
    }

}

.main-menu-bar {
    .navigation-toggle {
        display: none;
    }
}

.login {
    position: relative;
    height: 100vh;
    min-height: 800px;

    .header {
        background: transparent;
        min-height: 0;

        .logo {
            margin: 0 0 0 139px;
        }
    }

    &:before {
        position: absolute;
        //z-index: -1;
        width: 35%;
        height: 100%;
        left: 0;
        top: 0;
        bottom: 0;
        content: ' ';
        background-image: linear-gradient(149deg, var(--white), var(--pale-grey));
    }

    &:after {
        position: absolute;
        //z-index: -1;
        width: 35%;
        height: 100%;
        left: 0;
        top: 0;
        bottom: 0;
        content: ' ';
        background-image: url('assets/svg/lock.svg');
        background-position: center center;
        background-size: 100% auto;
        background-repeat: no-repeat;
    }

    .content {
        width: 51%;
        float: right;
        margin-top: 50px;
        padding: 0 17%;
    }
}

.menu-bar {
    list-style-type: none;
    margin: 28px 0 0 80px;
    padding: 0;
    overflow: hidden;
    display: inline-block;

    li {
        float: left;

        a {

            display: block;
            padding: 14px 16px;
            text-decoration: none;
            font-size: 19px;
            font-weight: bold;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.21;
            letter-spacing: normal;
            text-align: center;
            color: #2a2f33;

            &.active {
                text-decoration: underline;
            }
        }
    }
}

.footer {
    .row {
        padding-top: 22px;
        vertical-align: top;
        margin-right: -15px;
        margin-left: -15px;
    }

    .col {
        display: inline-block;
        width: 24%;
        float: left;
        padding-right: 15px;
        padding-left: 15px;
        position: relative;
        box-sizing: border-box;

        &:first-child {
            width: 28%;
        }

        .title {
            font-size: 23px;
            font-weight: bold;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.22;
            letter-spacing: normal;
            text-align: left;
            color: var(--brown-grey);
            padding: 0 0 10px 0;
        }

        .link {
            font-size: 13px;
            font-weight: 600;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.15;
            letter-spacing: normal;
            text-align: left;
            color: #2a2f33;
            text-decoration: none;
            display: block;
            padding: 5px 0;
        }
    }

    .split {
        clear: both;
        width: 100%;
        margin: 20px auto 0 auto;
        padding-top: 20px;
        height: 0;
        border-bottom: solid 2px var(--bluey-grey);
    }

    .big-text {
        width: 100%;
        font-size: 23px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.22;
        letter-spacing: normal;
        text-align: left;
        color: #2a2f33;
    }

    .logo {
        padding: 40px 0 40px 116px;
        font-size: 23px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.22;
        letter-spacing: normal;
        text-align: left;
        color: #2a2f33;
        background: url("./assets/logo.svg") left no-repeat no-repeat;
        background-size: 100px 100px;
    }

    .social-links {
        margin-top: 30px;
        .social-link {
            width: 42px;
            height: 42px;
            border: solid 2px #2a2f33;
            line-height: 1.22;
            border-radius: 50px;
            display: inline-block;
            text-decoration: none;
            color: #2a2f33;
            font-size: 21px;
            padding: 10px;
            margin-left: 10px;

            &:first-child {
                margin-left: 0;
            }
        }
    }

    .copyright {
        margin-top: 50px;
        font-size: 12px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.17;
        letter-spacing: normal;
        text-align: center;
        color: var(--brown-grey);
    }
}

.accordion {
    width: 100%;
    list-style-type: none;
    box-shadow: -10px 15px 35px 0 rgba(0, 0, 0, 0.16);
    border-radius: 10px;
    padding: 0;
    font-size: 17px;

    li {
        text-align: left;

        .header {
            padding: 10px 40px 10px 80px;
            background-color: var(--white);
            border: solid 1px #e9ecef;
            cursor: pointer;
            min-height: 0;
            &:after{
                content: '\e930';
                margin: 5px -20px 0 0;
                font-family: 'IconsFont' !important;
                color: var(--azul);
                float: right;
            }
        }

        .content {
            overflow: hidden;
            font-size: 16px;
            transition: max-height 0.2s;
            background-color: #edf3fe;
            max-height: 0;
            line-height: 24px;
        }

        &:not(first-child) .header {
            border-top: 0;
        }

        .content .wrapper {
            padding: 10px 10px 10px 80px;
        }

        &.active {
            .header:after{
                content: '\e934';
            }
            .content {
                max-height: 600px;
            }
        }

        &:first-child .header {
            border-radius: 10px 10px 0 0;
        }

        &:last-child .header {
            border-radius: 0 0 10px 10px;
        }

        &.active:last-child {
            .header {
                border-radius: 0;
            }

            .content {
                border-radius: 0 0 10px 10px;
            }
        }
    }
}

.faq {
    margin: 0;
    padding: 0;

    .support {
        font-size: 13px;
        color: #2a2f33;
        font-weight: 600;
        text-align: left;
        font-family: SFProDisplay;
    }
}

.main {
    min-height: 100vh;
    margin: auto;

    &.index {
        .header {
            background-image: linear-gradient(118deg, var(--white) 23%, var(--pale-grey) 100%);

            .title {
                position: relative;
            }

            .slider {
                min-height: 470px;

                .left {
                    margin-top: 210px;
                    width: 60%;
                }

                .image {
                    position: absolute;
                    right: 0;
                    top: -30px;
                    bottom: 0;
                    width: 60%;
                    background: url("./assets/svg/errors/error_comming.svg") center center no-repeat;
                }

                h1 {
                    font-size: 54px;
                    font-weight: bold;
                    font-stretch: normal;
                    font-style: normal;
                    line-height: 1.19;
                    letter-spacing: normal;
                    text-align: left;
                    color: #2a2f33;
                }

                h2 {
                    font-size: 26px;
                    font-weight: 500;
                    font-stretch: normal;
                    font-style: normal;
                    line-height: 1.19;
                    letter-spacing: normal;
                    text-align: left;
                    color: var(--bluey-grey);
                    width: 60%;
                }
            }
        }
    }

    .footer {
        background: var(--pale-grey);
    }

    .style {
        position: relative;
        min-height: 275px;
        padding: 140px 0;

        &:before {
            content: ' ';
            width: 322px;
            height: 244px;
            background: url("./assets/svg/fire.svg");
            -webkit-background-size: 322px 244px;
            background-size: 322px 244px;
            position: absolute;
            left: 0;
            bottom: 0;
            z-index: -1;
        }

        &:after {
            content: ' ';
            width: 566px;
            height: 435px;
            background: url("./assets/svg/octagonal-gray.svg");
            -webkit-background-size: 1140px 1140px;
            background-size: 1140px 1140px;
            background-position-x: 0;
            background-position-y: 435px;
            position: absolute;
            right: 0;
            top: 0;
            z-index: -1;
        }

    }
}

.main-content {
    margin-top: -69px;

    h1 {
        font-size: 40px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.2;
        letter-spacing: normal;
        color: #2a2f33;
    }

    h3 {
        font-size: 20px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.2;
        letter-spacing: normal;
        color: var(--bluey-grey);
    }

    .cols {
        display: flex;
        flex-direction: row;
        width: 100%;
        margin-bottom: 20px;

        .col {
            flex: 0 0 20%;

            h1 {
                font-size: 30px;
            }
        }
    }

    .circle {
        box-shadow: 0 10px 16px 0 rgba(0, 0, 0, 0.16);
        background-color: #f9fbfd;
        width: 90px;
        height: 90px;
        border-radius: 50px;
        font-size: 75px;
        margin: auto;
    }
}

.panel {
    margin-top: 26px;
    width: 100%;
    border-radius: 10px;
    box-shadow: -10px 15px 35px 0 rgba(0, 0, 0, 0.16);
    background-color: var(--white);

    .panel-content {
        padding: 46px;
    }
}

.user {
    position: relative;
    min-height: 520px;
    padding: 30px 0 30px 78px;
    transition: padding 0.5s;
    background-color: var(--pale-grey);

    .panel-header {
        position: absolute;
        top: 0;
        z-index: 2;
        left: 0;
        right: 0;
        height: 82px;
        box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
        background-color: var(--white);
    }

    .navigation {
        position: absolute;
        list-style-type: none;
        top: 0;
        bottom: 0;
        left: 0;
        width: 78px;
        box-shadow: 0 -15px 15px 0 rgba(0, 0, 0, 0.16);
        background-color: var(--white);
        padding: 25px 0;
        margin: 0;
        z-index: 1;
        transition: width 0.5s;
        overflow: hidden;

        .active-show {
            display: none;
        }

        span {
            display: inline-block;
        }

        li {
            margin-top: 53px;
            white-space: nowrap;
            text-align: left;

            &:first-child {
                margin-top: 0;
            }

            button {
                border: 0;
                background: transparent;
                padding: 0;
                margin: 0;
                outline: none;
            }

            button, a {

                color: var(--brownish-grey);
                text-decoration: none;
                text-align: left;

                i {
                    width: 78px;
                    display: inline-block;
                    text-align: center;
                    font-size: 25px;
                }

                &.active {
                    color: var(--azul);
                }
            }
        }
    }

    &.active .navigation {
        width: 208px;
    }

    &.active {
        padding-left: 208px;

        .active-show {
            display: block;
        }

        .active-hide {
            display: none;
        }
    }

    .content {
        padding: 0 35px;
        margin: auto;
        text-align: left;
        label{
            width: 50%;
            padding: 20px 2% 5px 2%;
            display: inline-block;
        }
    }

    .address-container {
        display: flex;
        height: 70px;
        margin-top: 40px;

        .admin-actions {
            float: right;
            width: auto;
            height: 70px;

            a, button {
                width: 38px;
                height: 70px;
                line-height: 70px;
                padding: 0 16px;
                color: black;
                font-size: 38px;
                text-decoration: none;
                text-underline: none;

                &.active {
                    color: var(--azul);
                }
            }
        }

        .address {
            text-align: center;
            font-size: 26px;
            letter-spacing: 8.58px;
            line-height: 70px;
            width: 100%;
            height: 70px;
            border-radius: 35px;
            box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.16);
            background-color: var(--white);
            padding: 0 10px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
    }

    .card-row{
        margin: 0 -15px;
        .card-col{
            float: left;
            width: 20%;
            margin: 30px 0 0 0;
            padding: 0 15px;
            &.custom {
                width: 40%;
                .card-holder{
                    -webkit-transition: all 200ms ease-in;
                    -moz-transition: all 200ms ease-in;
                    transition: all 200ms ease-in;
                    -webkit-transform: scale(1);
                    -ms-transform: scale(1);
                    -moz-transform: scale(1);
                    height: 115px;
                    margin-top: 20px;
                    h1{
                        margin: 0 0 0 30px;
                        height: 45px;
                    }
                    .row-content{
                        height: 70px;
                        border-radius: 35px;
                        box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.16);
                        background-color: var(--white);
                        span{
                            word-break:break-all;
                            padding: 5px 0;
                            text-align: center;
                        }
                    }
                    &:first-child{
                        margin-top: 0;
                    }
                    &:hover {
                        z-index: 2;
                        -webkit-transform: scale(1.1);
                        -ms-transform: scale(1.1);
                        -moz-transform: scale(1.1);
                        transform: scale(1.1);
                    }
                }
            }
            .card{
                height: 250px;
                box-shadow: -10px 15px 35px 0 rgba(0, 0, 0, 0.16);
                background-color: var(--white);
                -webkit-transition: all 200ms ease-in;
                -moz-transition: all 200ms ease-in;
                transition: all 200ms ease-in;
                text-align: center;
                -webkit-transform: scale(1);
                -ms-transform: scale(1);
                -moz-transform: scale(1);
                transform: scale(1);
                border-radius: 10px;
                .card-wrapper{
                    padding: 10px 15px;
                    .card-title {
                        font-size: 18px;
                        font-weight: bold;
                        font-stretch: normal;
                        font-style: normal;
                        line-height: 1.17;
                        color: #2a2f33;
                        margin-top: 10px;
                    }

                    .card-content {
                        margin-top: 10px;
                        font-size: 25px;

                        &.sublime {
                            font-size: 18px;
                        }

                        .timer-hour, .timer-minute, .timer-second {
                            display: inline-block;
                            font-size: 22px;
                            font-weight: 500;
                            font-stretch: normal;
                            font-style: normal;
                            line-height: 1.2;
                            letter-spacing: normal;
                            text-align: center;
                            color: #2a2f33;
                        }

                        .timer-hour {
                            font-size: 40px;
                        }
                    }

                    .card-title2 {
                        font-size: 20px;
                        font-weight: 300;
                        font-stretch: normal;
                        font-style: normal;
                        line-height: 1.2;
                        letter-spacing: normal;
                        color: #2a2f33;
                        margin-top: 25px;
                    }

                    .card-content i {
                        font-size: 13px;
                        margin-left: 10px;
                    }

                    .card-filter {
                        margin-top: 54px;
                        font-size: 11px;
                        font-weight: 500;
                        font-stretch: normal;
                        font-style: normal;
                        line-height: 1.18;
                        letter-spacing: normal;
                        color: #2a2f33;
                    }
                }
                &:hover {
                    z-index: 2;
                    -webkit-transform: scale(1.1);
                    -ms-transform: scale(1.1);
                    -moz-transform: scale(1.1);
                    transform: scale(1.1);
                }
                &.card-half{
                    height: 115px;
                    margin-top: 20px;
                    &:first-child{
                        margin-top: 0;
                    }
                }
            }
        }
    }

    .split-panel {
        .split-panel-form {
            float: left;
            width: 30%;
            padding: 0 5% 0 15%;
            border-right: solid 2px var(--bluey-grey);

            label {
                margin-bottom: 10px;
            }

            input {
                padding: 0 10px;
            }

            .btn {
                display: inline-block;
                padding: 8px 40px;
            }
        }

        .split-panel-text {
            float: right;
            width: 45%;
            font-size: 16px;
            font-weight: 500;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.19;
            letter-spacing: normal;
            text-align: left;
            color: #2a2f33;
        }
    }

    .chartFilter {
        float: right;
        margin-top: 30px;
    }

    .footer {
        box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.16);
        background-color: var(--white);
    }
}

.chart-filter {
    float: right;
    margin-top: 30px;
    position: relative;
    //display: inline-block;
    color: black;
    padding: 15px;
    text-align: center;
    font-size: 16px;

    .chart-filter-content {
        visibility: hidden;

        border-radius: 10px;
