                height: 76px;
                .content-wrapper{
                    height: 76px;
                    padding: 0 0 0 56px;
                    .logo{
                        width: 180px;
                        height: 76px;
                        padding-right: 2px;
                        .head-title{
                            font-size: 12px;
                        }
                        .head-sub-title{
                            font-size: 8px;
                        }
                    }
                    .action-button{
                        margin: 0 30px 0 5px;
                        float: right;
                        .set-api-key {
                            display: none;
                        }
                    }
                    .proxy-status{
                        background-image: none;
                        height: 100%;
                        vertical-align: middle;
                        margin: 0 10px;
                        width: auto;
                        i {
                            width: auto;
                            height: auto;
                            padding: 8px;
                            color: #2a2f33;
                            border: solid 2px #2a2f33;
                            box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.16);
                            background-color: transparent;
                            border-radius: 30px;
                            position: relative;
                            line-height: 76px;
                            right: auto;
                            bottom: auto;
                        }
                    }
                }
                .navigation {
                    width: 56px;
                    top: 0;
                    box-shadow: none;
                    height: 24px;
                    background: transparent;
                    overflow: hidden;
                    padding: 25px 0;
                }
                &.pr-success{
                    background: #8ac34a;
                }
                &.pr-warning{
                    background: #f8b64d;
                }
                &.pr-error{
                    background: #dd4636;
                }
            }
            .logo {
                width: 250px;
                height: 80px;
                background: url("assets/logo.svg") 10px center no-repeat;
                background-size: 50px 50px;
                display: inline-block;
                float: left;
                padding: 20px 20px 20px 70px;

                .head-title {
                    font-size: 23px;
                    font-weight: bold;
                    color: #2a2f33;
                    margin: 0;
                    display: block;
                    height: 50%;
                    vertical-align: bottom;
                    text-align: left;
                }

                .head-sub-title {
                    font-size: 15px;
                    font-weight: 500;
                    font-stretch: normal;
                    font-style: normal;
                    line-height: 1.2;
                    letter-spacing: normal;
                    text-align: left;
                    color: #2a2f33;
                    margin-top: 5px;
                }
            }
            &.active{
                padding: 86px 0;
            }
            .action-button {
                float: left;
                vertical-align: middle;
                height: 100%;

                .floater {
                    float: left;
                    height: 50%;
                    width: 100%;
                    margin-bottom: -19px;
                }
            }

        }
        &.active{
            padding-left: 0;
            .navigation{
                position: fixed;
                left: 0;
                right: 0;
                bottom: 0;
                top: 0;
                padding: 20px 0;
                overflow: scroll;
                width: auto;
            }
        }
        .content{
            padding-top: 94px;
        }
        .navigation-menu{
            &:before{
                border-top: 2px solid #2a2f33;
                content: ' ';
                position: relative;
                width: 30%;
                display: block;
                opacity: 0.8;
                height: 0;
                padding-bottom: 36px;
            }
            .title{
                display: block;
                font-size: 16px;
                &:after{
                    display: inline-block;
                    margin-left: 10px;
                    content: '\e930';
                    font-family: 'IconsFont' !important;
                }
            }
            color: var(--brownish-grey);
            display: block;
            margin-left: 36px;
            .menu-bar-navigation {
                max-height: 0;
                font-size: 16px;
                list-style-type: none;
                margin: 36px 0 0 36px;
                padding: 0;
                overflow: hidden;
                display: inline-block;
                transition: width 0.5s ease-in-out;
                -webkit-transition: width 0.5s ease-in-out;
                -moz-transition: width 0.5s ease-in-out;
                -o-transition: width 0.5s ease-in-out;
                li {
                    a {
                        display: block;
                        padding: 0;
                        text-decoration: none;
                        line-height: 1.21;
                        letter-spacing: normal;
                        text-align: left;
                        color: var(--brownish-grey);
                    }
                }
            }
            &.toggled{
                .title{
                    &:after{
                        content: '\e934';
                    }
                }
                .menu-bar-navigation{
                    max-height: 1000px;
                }
            }
        }
    }
}

@media (max-width: 576px) {
    .panel-earning {
        width: auto;
        margin: auto;
    }
    .header {
        .main-menu-bar {
            .navigation-toggle {
                margin: 36px 0 36px 36px;
            }
        }
    }
    .main-content {
        .cols {
            .col {
                h1 {
                    font-size: 20px;
                }
                h3{
                    font-size: 14px;
                }
            }
        }
    }
    .footer {
        .logo{
            background: none;
            text-align: center;
            vertical-align: text-top;
            position: relative;
            display: inline-block;
            padding: 0 0 0 80px;
            line-height: 80px;
            height: 80px;

            &:before{
                position: absolute;
                content: ' ';
                background: url("assets/logo.svg") center no-repeat;
                width: 80px;
                height: 80px;
                display: block;
                left: 0;
            }
        }
        .row:nth-child(n+2){
            padding: 0 0 10px 0;
            .col{
                width: 100% !important;
                text-align: center;
            }
        }
    }
    .header {
        .main-menu-bar {
            .menu-bar {
                margin-top: 20px;
            }
        }
    }
    .user {
        .card-row{
            .card-col{
                .card{
                    .card-wrapper{
                        .card-content {
                            margin-top: 3px;
                            font-size: 15px;
                        }
                    }
                }
            }
        }
        &.proxy{
            .panel-header{
                .content-wrapper{
                    height: 76px;
                    padding: 0 0 0 36px;
                    .logo{
                        width: 180px;
                    }
                    .action-button{
                        margin: 0 5px 0 5px;
                    }
                    .proxy-status{
                        margin: 0 5px;
                    }
                }
                .navigation {
                    width: 36px;
                    li{
                        button{
                            i{
                                width: 36px;
                            }
                        }
                    }
                }
            }
            .navigation {
                top: 76px;
            }
        }
    }
}
